<template>
  <div>
    <Width100>
      <Paragraph
        title="THE DESIGN PROCESS"
        subtitle="THE PROBLEM"
        paragraph="The arrangement and styling of the headlines, text, and images on the page are very cluttered. The text information on the side makes the UI distracting and hard to navigate. Hierarchy of information is not very strong and balanced. For example for the main Highlights, some images are 100% vs 50% vs 30% and that section only takes up 80% of the screen, while the other 20% are latest news and ads."
        v-bind:center="true"
      />
    </Width100>

    <Width100>
      <div class="img-container">
        <VueImage :src="ign_esports_page2" alt="IGN Esports page" />
      </div>
    </Width100>
    <LineBreak />

    <Width100>
      <Paragraph
        paragraph="The mobile version of the esport page is more focused and clean and my goal is to carry that over for desktop as well."
        v-bind:center="true"
      />
    </Width100>

    <Width100>
      <div class="img-container">
        <VueImage :src="ign_mobile" alt="IGN Mobile View" />
      </div>
    </Width100>
    <LineBreak />

    <Width100>
      <Paragraph
        paragraph="Schedule tab is confusing because it lists 3 cards on the side with dates. It was not clear at first if they were related to the schedule. I believe the UI might also look odd if there are no upcoming events since it will lead to too much space and the user may perceive the design as bland."
        v-bind:center="true"
      />
    </Width100>

    <Carousel
      v-bind:items="scheduleCarouselItems"
      :containerStyle="containerStyle"
      :imgStyle="imgStyle"
    />
    <LineBreak />

    <Width100>
      <Paragraph
        paragraph="Top navigation bar is not consistent when navigating through the website. With inconsistent UI, users have to relearn what to do when they encounter one."
        v-bind:center="true"
      />
    </Width100>

    <Carousel
      v-bind:items="navigationCarouselItems"
      :containerStyle="containerStyle"
      :imgStyle="imgStyle"
    />
    <LineBreak />
  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import Paragraph from '../common/Paragraph';
import LineBreak from '../common/LineBreak';
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import ign_esports_page2 from "../../assets/img/ign_esports_page2.png";
import ign_mobile from "../../assets/img/ign_mobile.jpg";
import ign_schedule_page1 from "../../assets/img/ign_schedule_page1.png";
import ign_schedule_page2 from "../../assets/img/ign_schedule_page2.png";
import ign_navigation_bar1 from "../../assets/img/ign_navigation_bar1.png";
import ign_navigation_bar2 from "../../assets/img/ign_navigation_bar2.png";

export default {
    components: {
      Carousel,
      LineBreak,
      Paragraph,
      VueImage,
      Width50,
      Width100,
    },

    data() {
      return {
        ign_esports_page2: ign_esports_page2,
        ign_mobile: ign_mobile,
        scheduleCarouselItems: [{
          img: ign_schedule_page1,
          alt: "IGN Schedule Page"
        }, {
          img: ign_schedule_page2,
          alt: "IGN Schedule Page"
        }],
        navigationCarouselItems: [{
          img: ign_navigation_bar1,
          alt: "IGN Navigation Bar"
        }, {
          img: ign_navigation_bar2,
          alt: "IGN Navigation Bar"
        }],
        containerStyle: {
          height: "100%"
        },
        imgStyle: {
          padding: "24px",
          width: "80%",
          margin: "0 auto",
        },
      };
    },

};
</script>

<style lang="scss" scoped>
.img-container {
  padding: 0 32px;
  max-width: 720px;
  margin: 32px auto;
}
</style>



