<template>
  <div>
    <Landing />
    <BeforeAfter />
    <DesignProcess />
    <Solution />
    <FinalMockUps />
  </div>
</template>

<script>
import Landing from './Landing';
import BeforeAfter from "./BeforeAfter";
import DesignProcess from "./DesignProcess";
import Solution from "./Solution";
import FinalMockUps from "./FinalMockUps";

export default {
    name: 'IGN',
    components: {
      Landing,
      BeforeAfter,
      DesignProcess,
      Solution,
      FinalMockUps,
    },
    mounted () {
      this.$ga.page({
        page: '/ign',
        title: 'Ign',
        location: window.location.href
      });
    },

};
</script>



