<template>
  <div>
    <Width100>
      <Paragraph
        subtitle="Final Mockups"
        paragraph="Final mockup for desktop and mobile"
        v-bind:center="true"
      />
    </Width100>

    <Carousel
      v-bind:items="finalMocksCarouselItems"
      :containerStyle="containerStyle"
      :imgStyle="imgStyle"
    />
  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import Paragraph from '../common/Paragraph';
import OrderedList from '../common/OrderedList';
import LineBreak from '../common/LineBreak';
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import ign_desktop_v1 from "../../assets/img/ign_desktop_v1.jpg";
import ign_desktop_v2 from "../../assets/img/ign_desktop_v2.jpg";
import ign_mobile_v1 from "../../assets/img/ign_mobile_v1.jpg";
import ign_mobile_v2 from "../../assets/img/ign_mobile_v2.jpg";

export default {
    components: {
      Carousel,
      LineBreak,
      OrderedList,
      Paragraph,
      VueImage,
      Width50,
      Width100,
    },
    data() {
      return {
        finalMocksCarouselItems: [{
          img: ign_desktop_v1,
          alt: "IGN final mock Desktop version 1",
        }, {
          img: ign_desktop_v2,
          alt: "IGN final mock Desktop version 2",
        },{
          img: ign_mobile_v1,
          alt: "IGN final mock mobile version 1",
        },{
          img: ign_mobile_v2,
          alt: "IGN final mock mobile version 2",
        },],
        containerStyle: {
          height: "1000px"
        },
        imgStyle: {
          padding: "24px",
          height: "100%",
          margin: "0 auto",
        },
      };
    },
};
</script>

<style lang="scss" scoped>
</style>



