<template>
  <div>
    <Width100>
      <div class="img-container">
        <VueImage :src="beforeafter" alt="Before and After" />
      </div>
    </Width100>
    <LineBreak />
  </div>
</template>

<script>
import LineBreak from '../common/LineBreak';
import VueImage from '../common/VueImage';
import Width100 from '../common/layout/Width100';

import beforeafter from "../../assets/img/beforeafter.png";

export default {
    components: {
      LineBreak,
      VueImage,
      Width100,
    },

    data() {
      return {
        beforeafter: beforeafter
      };
    },

};
</script>

<style lang="scss" scoped>
.img-container {
  padding: 0 32px;
  margin: 32px auto;
}
</style>



